<template>
  <div class="square">
    <!-- <div class="square_header">
      <img class="square_header_left" @click="backClick" src="../../assets/images/left.png" alt />
      <img class="square_header_right" src="../../assets/images/share.png" alt />
    </div>-->
    <!-- <van-nav-bar            
        left-arrow
        title=""
        @click-left="onClickLeft"
    >    
    </van-nav-bar>-->
    
      <div class="main" style="overflow-y: scroll;height: 100%;-webkit-overflow-scrolling: touch;padding-bottom: 80px" >
        <div class="list">
          <div class="header">
            <div class="avatar-box" @click="handleToMyPage(data)">
              <img :src="data.avatar" class="portrait_img" alt />
              <img
                class="isvip"
                src="../../assets/images/icon_isvip.png"
                v-if="data.is_ident_icon == 1"
              />
            </div>
            <div class="header_center">
              <span class="header_center_name">{{data.author}}</span>
              <div class="header_center_label">
                <div class="label_grade">
                  <img
                    class="label_grade_img"
                    v-if="data.is_carvip == 1"
                    src="../../assets/images/icon_iscarvip.png"
                    alt
                  />
                  <span class="label_grade_name">
                    <i v-show="data.is_ident_icon==1">{{data.show_car}}</i>
                    <i>{{data.level}}</i>
                  </span>
                </div>
                <div class="label_heat">
                  <img class="label_heat_img" src="../../assets/images/icon_label.png" alt />
                  <span class="label_grade_name">{{data.extcredits2}}°</span>
                </div>
              </div>
            </div>
            <div class="header_right">
              <span class="header_right_monry" v-if="data.reward_price != 0">¥{{data.reward_price}}</span>
              <!-- <span class="header_right_time">{{item.time_string}}</span> -->
            </div>
          </div>
          <div class="answers_content">
            <span class="answers_content_text">{{data.message}}</span>
          </div>
          <div class="answers_img">
            <template v-if="data.image_list != [] && data.image_list.length > 1">
              <img
                v-for="(item,index) in data.image_list"
                @click="answersImageClick(data.image_list,index)"
                :key="index"
                :src="item"
                class="answers_image"
              />
            </template>
            <template v-if="data.image_list != [] && data.image_list.length == 1">
              <img
                v-for="(item,index) in data.image_list"
                @click="answersImageClick(data.image_list,index)"
                :key="index"
                :src="item"
                class="answers_image_one"
              />
            </template>
            <template v-if="data.image_list == []">
              <div></div>
            </template>
          </div>
          <!--投票-->
          <div class="vote" v-if="data.special==1">
            <div class="vote_header">
              <span class="vote_header_left">投票选项</span>
              <div class="option_up_right" v-show="All.poll && All.poll.is_expiration == 0">
                <span class="option_up_right_text">据截止还有</span>
                <span class="option_up_right_time">
                  <van-count-down :time="times" />
                </span>
              </div>
              <span v-show="All.poll && All.poll.is_expiration == 1" class="vote_header_right">投票已结束</span>
            </div>
            <div class="vote_center_content">
              <span
                class="vote_center"
                v-show="All.poll && All.poll.is_expiration == 0"
              >{{All.poll && All.poll.reward_msg}}</span>
            </div>
            <div class="vote_list" v-for="(item1,key) in All.polloption" :key="key">
              <div class="vote_list_left">
                <img :src='item1.icon' class="vote_list_left_img" alt />
                <span class="vote_list_left_span">{{item1.polloption}}</span>
              </div>
              <div class="vote_list_right">
                <img
                  v-show="All.poll.is_vote == item1.polloptionid"
                  src="../../assets/images/icon_voted.png"
                  class="vote_list_right_img"
                  alt
                />
                <span v-show="All.poll.is_expiration == 1" class="vote_list_right_span">{{item1.votes}}</span>
              </div>
              <div class="vote_list_right" v-show="All.poll.is_expiration == 0">
                <span
                  class="vote_list_right_bottom"
                  @click="voteOwnClick(item1.polloptionid,All.thread_data.tid)"
                >投票</span>
              </div>
            </div>
            <span class="vote_span" v-if="All.poll">共{{All.poll.voters}}人投票</span>
          </div>
          <div class="type">
            <div class="type_left">
              <div class="type_vehicle">
                <span class="type_vehicle_title">谁可以回答(车主)：</span>
                <span class="type_vehicle_name" v-if="data.is_reply == 1">所有人</span>
                <span class="type_vehicle_name" v-else>不能回答</span>
              </div>
            </div>
            <span class="type_right">{{data.time_string}}</span>
          </div>
          <!--没人回答-->
          <div
            class="noList"
            v-show="list.selected && list.selected.length == 0 && list.post_list && list.post_list.length == 0 && data.is_self != 0"
          >
            <img class="noList_img" src="../../assets/images/pic_wait.png" alt />
            <span class="noList_text">车友们正在赶来的路上</span>
            <span class="noList_span">请耐心等耐...</span>
          </div>
          <!--没人回答-->
          <div
            class="noList"
            v-show="list.selected && list.selected.length == 0 && list.post_list && list.post_list.length == 0 && data.is_self == 0"
          >
            <img class="noList_img" src="../../assets/images/pic_comment.png" alt />
            <span class="noList_text">暂无回答，快来抢答领赏~</span>
            <!-- <span class="noList_span">请耐心等耐...</span> -->
          </div>
          <!-- <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad"> -->
          <!--精选-->
          <div class="content" v-show="list.selected && list.selected.length != 0 ">
            <div class="list_title answers_news">
              <span class="list_title_down">精选回答</span>
              <img
                v-show="data.is_reward_icon == 2"
                class="answers_news_up_img"
                src="../../assets/images/icon_equalization.png"
                alt
              />
            </div>
            <div class="item" v-for="(item2,index) in list.selected" :key="index">
              <div class="header">
                <div class="avatar-box" @click="handleToMyPage(data)">
                  <img :src="item2.author_avatar" class="portrait_img" alt />
                  <img
                    class="isvip"
                    src="../../assets/images/icon_isvip.png"
                    v-if="item2.is_ident_icon == 1"
                  />
                </div>
                <!-- <img :src="item2.author_avatar" class="portrait_img" alt /> -->
                <div class="header_center">
                  <span class="header_center_name">{{item2.author}}</span>
                  <div class="header_center_label">
                    <div class="label_grade">
                      <img
                        class="label_grade_img"
                        v-if="item2.is_carvip == 1"
                        src="../../assets/images/icon_iscarvip.png"
                        alt
                      />
                      <span class="label_grade_name">
                        <!-- <i>领克</i> -->
                        <i>{{item2.level}}</i>
                      </span>
                    </div>
                    <div class="label_heat">
                      <img class="label_heat_img" src="../../assets/images/icon_label.png" alt />
                      <span class="label_grade_name">{{item2.extcredits2}}°</span>
                    </div>
                  </div>
                </div>
                <!--投票-->
                <div class="header_right_vote" v-show="data.special==1">
                  <div class="header_right_vote_zan">
                    <img
                      @click="normalClick(item2.tid,item2.pid)"
                      class="header_right_vote_zan_img"
                      :src="item.is_zan==0 || item.is_zan == 2?iconLoveNormal:iconLoveSelection"
                      alt
                    />
                    <span class="header_right_vote_zan_span">{{item2.zan}}</span>
                  </div>
                  <div class="header_right_vote_zan">
                    <img
                      class="header_right_vote_zan_img"
                      @click="steponSelectedClick(item2.tid,item2.pid)"
                      :src="item.is_zan==2?iconSteponSelected:iconSteponNormal"
                    />
                    <span class="header_right_vote_zan_span">{{item2.cai}}</span>
                  </div>
                </div>
              </div>
              <div class="answers_contents" v-show="data.special==1">
                <span class="answers_contents_text">{{item2.message}}</span>
              </div>
              <div
                class="answers_newst"
                v-show="item.reply_num !=0"
                v-for="(item4,key) in item.reply"
                :key="key"
              >
                <div class="answers_newst_up">
                  <span class="answers_newst_up_text">{{item4.author}} 回答：</span>
                </div>
                <span class="answers_newst_dow">{{item4.message}}</span>
              </div>
            </div>
          </div>
          <!--最新-->
          <div class="content" v-show="list.post_list && list.post_list.length != 0 ">
            <div class="list_title answers_news">
              <span class="list_title_down">最新回答</span>
              <img
                v-show="data.is_reward_icon == 2"
                class="answers_news_up_img"
                src="../../assets/images/icon_equalization.png"
                alt
              />
            </div>
            <div class="item" v-for="(item,index) in list.post_list" :key="index">
              <div class="header">
                <div class="avatar-box" @click="handleToMyPage(data)">
                  <img :src="item.author_avatar" class="portrait_img" alt />
                  <img
                    class="isvip"
                    src="../../assets/images/icon_isvip.png"
                    v-if="item.is_ident_icon == 1"
                  />
                </div>
                <div class="header_center">
                  <span class="header_center_name">{{item.author}}</span>
                  <div class="header_center_label">
                    <div class="label_grade">
                      <img
                        class="label_grade_img"
                        v-if="item.is_carvip == 1"
                        src="../../assets/images/icon_iscarvip.png"
                        alt
                      />
                      <span class="label_grade_name">
                        <!-- <i>领克</i> -->
                        <i>{{item.level}}</i>
                      </span>
                    </div>
                    <div class="label_heat">
                      <img class="label_heat_img" src="../../assets/images/icon_label.png" alt />
                      <span class="label_grade_name">{{item.extcredits2}}°</span>
                    </div>
                  </div>
                </div>
                <!--投票-->
                <div class="header_right_vote" v-show="data.special==1">
                  <div class="header_right_vote_zan">
                    <img
                      @click="normalClick(item.tid,item.pid)"
                      class="header_right_vote_zan_img"
                      :src="item.is_zan==0 || item.is_zan == 2?iconLoveNormal:iconLoveSelection"
                      alt
                    />
                    <span class="header_right_vote_zan_span">{{item.zan}}</span>
                  </div>
                  <div class="header_right_vote_zan">
                    <img
                      class="header_right_vote_zan_img"
                      @click="steponSelectedClick(item.tid,item.pid)"
                      :src="item.is_zan==2?iconSteponSelected:iconSteponNormal"
                    />
                    <span class="header_right_vote_zan_span">{{item.cai}}</span>
                  </div>
                </div>
              </div>
              <div class="answers_contents" v-show="data.special==1">
                <span class="answers_contents_text">{{item.message}}</span>
              </div>
              <div
                class="answers_newst"
                v-show="item.reply_num !=0"
                v-for="(item4,key) in item.reply"
                :key="key"
              >
                <div class="answers_newst_up">
                  <span class="answers_newst_up_text">{{item4.author}} 回答：</span>
                </div>
                <span class="answers_newst_dow">{{item4.message}}</span>
              </div>
            </div>
          </div>
          <!-- </van-list> -->

        </div>
      </div>
      <div class="input_item" v-show="data.is_self == 0 && data.is_reply ==1">
        <van-field
          class="input_item_textarea"
          @focus="animateWidth('company_name','blur')"
          @blur="blurClick"
          v-model="postParameter.message"
          placeholder="补充答案~"
          :border="false"
          @keyup.enter="addPost"
        >
        <span slot="button" class="input_content_button" @click="addPost">发布</span>
        </van-field>
      </div>
 

  </div>
</template>
<script>
import { Dialog, ImagePreview,Toast } from "vant";
import { getFromNom } from "@js/moment";
export default {
  components: {
    [Dialog.Component.name]: Dialog.Component
  },
  data() {
    return {
      iconLoveNormal: require("../../assets/images/icon_love_normal.png"),
      iconLoveSelection: require("../../assets/images/icon_love_selection.png"),
      iconSteponSelected: require("../../assets/images/icon_stepon_selected.png"),
      iconSteponNormal: require("../../assets/images/icon_stepon_normal.png"),
      iconUserSelecte: require("../../assets/images/icon_user_selecte.png"),
      iconUserNormal: require("../../assets/images/icon_user_normal.png"),
      is: 0,
      data: {},
      All: {},
      list: [],
      allpage: -1,
      loading: false,
      finished: false,
      item: {
        token: "",
        tid: "",
        page_index: 0,
        page_size: 10
      },
      ImagePreviewDialog: "", //查看图片
      parameter: {
        //点赞参数
        token: this.$store.state.userData,
        type: "",
        pid: "",
        tid: "",
      },
      postParameter:{
        token: this.$store.state.userData,
        message:'',
        tid:'',
        uppid:0,
        reply_pid:'',
        attachment:0,
        aid_list:'',
      },
      time:'',
      times:''
    };
  },
  mounted() {
    this.time = new Date().getTime()
    this.init();
    document.title = " ";
  },
  filters: {
    timeformat(v) {
      if (v) {
        return getFromNom(v * 1000);
      } else {
        return "";
      }
    }
  },
  methods: {
    init() {
      (this.item.token = this.$store.state.userData),
        (this.item.tid = this.$route.query.id),
        //回复详情
        this.api.answersDetails(this.item).then(res => {
          if (res.err_code == 0) {
            this.data = res.data.thread_data;
            this.All = res.data;
            if(this.All.poll) {
              this.times = this.All.poll.expiration*1000 - new Date().getTime();
            }
            this.detailsItem();
          }
        });
    },
    //回复详情列表
    detailsItem() {
      this.api.getPostDetailComment(this.item).then(res => {
        if (res.err_code == 0) {
          this.allpage = res.data.total_page;
          this.list = res.data;
        }
        this.loading = false;
      });
    },
     // 跳转我的页面
    handleToMyPage(item){
      if(!this.$store.state.userData){
        //  window.location.href= "teldapp://login"
          let frame = document.createElement("frame");
              frame.src ="teldapp://login";
          document.documentElement.childNodes[0].appendChild(frame);        
          return null;
      }
      if(item.is_self == 1){
        this.$router.push({path:'/my/index/square'})
      }else {
        this.$router.push({path:'/my/index/square',query:{uid:item.authorid}})
      }
      //this.$router.push({path:'/my/index/square',query:{uid:''}})
    },
    onLoad() {
      if (this.list.length) {
        this.item.page_index++;
        this.detailsItem();
      }
      if (this.item.page_index == this.allpage) {
        this.finished = true;
      }
    },
   // 输入框获取焦点
    animateWidth() {
      // setTimeout(function(){
      window.scrollTo(0, document.body.scrollHeight);
      // this.inputShow = false;
      // },300);
    },
    blurClick() {

    },
    //回答
    addPost() {
      if (!this.$store.state.userData) {
      //  window.location.href = "teldapp://login";
      let frame = document.createElement("frame");
            frame.src ="teldapp://login";
        document.documentElement.childNodes[0].appendChild(frame);
        return null;
      }
      if (!this.postParameter.message) {
        return Toast("输入不能为空");
      }
      Toast.loading({
        message: "加载中...",
        forbidClick: true
      });
      this.postParameter.tid = this.data.tid;
      this.postParameter.reply_pid = this.data.pid;
      this.api.addPost(this.postParameter).then(res => {
        if(res.err_code == 0) {
          this.$toast('发布成功！')
          this.init()
        }
        console.log(res)
      })
    },
    //投票
    voteOwnClick(optionid, tid) {
      this.api
        .addVote({
          token: this.$store.state.userData,
          tid: tid,
          optionid: optionid
        })
        .then(res => {
          if (res.err_code == 0 && res.data == "") {
            this.$toast("投票成功");
            this.init();
          } else if (res.data.status == -1) {
            this.$toast(res.data.msg);
          }
        });
    },
    //返回上一页
    onClickLeft() {
      this.$router.go(-1);
    },
    //评论点赞
    normalClick(id, pid) {
      this.parameter.tid = id;
      this.parameter.pid = pid;
      this.parameter.type = 1;
      this.api.addZanPost(this.parameter).then(res => {
        if (res.err_code == 0) {
          for (let i = 0; i < this.list.post_list.length; i++) {
            if (this.list.post_list[i].tid == id) {
              this.list.post_list[i].is_zan = 1;
            }
          }
          for (let i = 0; i < this.list.selected.length; i++) {
            if (this.list.selected[i].tid == id) {
              this.list.selected[i].is_zan = 1;
            }
          }
          this.detailsItem();
          this.$toast(res.data.msg);
        }
      });
    },
    //评论点踩
    steponSelectedClick(id, pid) {
      this.parameter.tid = id;
      this.parameter.pid = pid;
      this.parameter.type = 2;
      this.api.addZanPost(this.parameter).then(res => {
        if (res.err_code == 0) {
          for (let i = 0; i < this.list.post_list.length; i++) {
            if (this.list.post_list[i].tid == id) {
              this.list.post_list[i].is_zan = 2;
            }
          }
          for (let i = 0; i < this.list.selected.length; i++) {
            if (this.list.selected[i].tid == id) {
              this.list.selected[i].is_zan = 2;
            }
          }
          this.detailsItem();
          this.$toast(res.data.msg);
        }
      });
    },
    //列表 点赞
    postZanClick(id) {
      this.parameter.tid = id;
      this.parameter.pid = "";
      this.parameter.type = 1;
      this.api.addZan(this.parameter).then(res => {
        if (res.err_code == 0) {
          this.data.is_zan = 1;
          this.$toast(res.data.msg);
          this.init();
        }
      });
    },
    //列表 点踩
    postCaiClick(id) {
      this.parameter.tid = id;
      this.parameter.pid = "";
      this.parameter.type = 2;
      this.api.addZan(this.parameter).then(res => {
        if (res.err_code == 0) {
          this.data.is_zan = 2;
          this.$toast(res.data.msg);
          this.init();
        }
      });
    },
    //回答列表 详情
    answersDetailsClick(pid) {
      let url = `${this.$store.state.indexUrl +
          "answers/answersDetails/askingDetails?pid=" +
          pid +
          "&tid=" +
          this.data.tid +
          "&" +
          this.$store.state.appParams}`,
        json = { url: url, share: "0" };
      window.location.href =
        `teldapp://newWebview?params=` + JSON.stringify(json);
      // this.$router.push({
      //   path: "/answers/answersDetails/askingDetails",
      //   query: { pid: pid, tid: this.data.tid }
      // });
    },
    //查看图片
    answersImageClick(image, index) {
      this.ImagePreviewDialog = ImagePreview({
        images: image,
        startPosition: index,
        loop:false
      });
    }
  }
};
</script>
<style lang="less" scoped rel="stylesheet/less">
.square_header {
  height: 67px;
  border-bottom: 1px solid #888888;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #ffffff;
  z-index: 3;
  .square_header_left {
    width: 16.5px;
    height: 16.5px;
    margin: 15px 0 0 15px;
  }
  .square_header_right {
    width: 20px;
    height: 20px;
    margin: 15px 15px 0 0;
  }
}
.square {
  // display: flex;
  // flex-direction: column;
  height: 100vh;
  box-sizing: border-box;
  position: relative;
  overflow: hidden;
  .list {
    padding: 15px 15px;
  }
}
.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  /* flex-wrap: nowrap; */
  width: 100%;
  align-items: center;
  height: 50px;
}
.header_center {
  display: flex;
  flex-direction: column;
  width: 200px;
}
.avatar-box {
  position: relative;
  margin-right: 13px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.portrait_img {
  width: 42px;
  height: 42px;
  border-radius: 100% 100%;
  flex: none;
  border: 1px solid;
}
.isvip {
  position: absolute;
  width: 14px;
  height: 14px;
  bottom: 6px;
  right: 1px;
}
.header_center_label {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  margin-top: 4.5px;
}
.header_center_name {
  font-size: 16px;
  color: #4e5578;
}
.label_grade {
  //   width: 92.5px;
  height: 15px;
  border: 1px solid #0ec6f1;
  border-radius: 5px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: center;
  padding: 0 5px;
}
.label_grade_img {
  width: 10px;
  height: 9px;
}
.label_grade_name {
  font-size: 10px;
  color: #0ec6f1;
  margin-left: 2px;
}
.label_heat {
  // width: 37.5px;
  height: 15px;
  border: 1px solid #0ec6f1;
  border-radius: 5px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: center;
  margin-left: 10px;
  padding: 0 5px;
}
.label_heat_img {
  width: 8px;
  height: 13px;
}
.header_right {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  justify-content: flex-end;
  height: 50px;
  .header_right_monry {
    color: #ef5277;
    font-size: 16.94px;
  }
